.updatePasswordContainer{
    width: 100vw;
    height:100vh;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(196, 196, 196);
    overflow: hidden;
}
.updatePasswordBox{
    background-color: white;
    width: 25vw;
    height:70vh;
    box-sizing: border-box;
    overflow: hidden;

}

.updatePasswordHeading{
    text-transform: uppercase;
    text-align: center;
    color: rgba(0, 0, 0, 0.688);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.181);
    width: 50%;
    margin: auto;

}

.updatePasswordBox>div>button{
    background-color: tomato;
    height: 3px;
    width: 50%;
    border: none;
    transition: all 0.5s;
}

.updatePasswordform{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
}

.updatePasswordform > div{
    display: flex;
    width: 100%;
    align-items: center;
}

.updatePasswordform > div > input{
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0,0.267);
    font:200 1.1vmax "Open Sans";
    outline: none;
}

.updatePasswordform > div > svg{
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: tomato;
}

.updatePasswordBtn{
    text-transform: uppercase;
    border: none;
    background-color: tomato;
    color: white;
    letter-spacing: 0.2rem;
    font: 400 01.2vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.237);
}

.updatePasswordBtn:hover{
    background-color: rgb(223, 81, 56);
   
}



@media screen and (max-width:600px) {
    .updatePasswordContainer{
        background-color: white;
    }
    .updatePasswordBox{
        width: 100%;
        height: 95vh;
    }
    .updatePasswordform{
       padding: 3em;
    }
    .updatePasswordform > div > input{
        padding: 2.5vmax 5vmax;
        padding-left: 7vmax;
        font-size: 2em;
    }
    .updatePasswordform > div > svg{
        font-size: 2.5em;
    }
    .updatePasswordBtn{
        font: 400 2em "Roboto";
        padding: 1.8vmax;
    }
    .updatePasswordform > a{
        font-size: 2em;
    }
}